.section-group {
  background-image: url("../img/section-group__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 14.6875rem 0 8.4375rem;
}
.main {
  color: #ffffff;
  padding: 0 0 20rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 35.625rem;
    height: 35.625rem;
    background: radial-gradient(50% 50% at 50% 50%, #F3D545 0%, rgba(255, 255, 255, 0.00) 100%);
    filter: blur(6.25rem);
    border-radius: 50%;
  }
  &__inner {
    position: relative;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    .main__img {
      display: none;
    }
  }
  h1 {
    font-size: 6.25rem;
    line-height: 100%;
    font-weight: 300;
    text-transform: lowercase;
    span {
      font-weight: 700;
      color: #FDC32E;
    }
  }
  &__subtitle {
    font-size: 4.5rem;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: -1.536px;
    text-transform: lowercase;
    margin-top: 1.25rem;
  }
  &__text {
    position: relative;
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 700;
    padding-left: 1.6875rem;
    margin-top: 3.125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.75rem;
      left: 0;
      width: 0.6875rem;
      height: 0.6875rem;
      background: #FFF;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 17.25rem;
  }
  &__info {
    position: relative;
    margin-left: 2rem;
    p {
      font-size: 1.25rem;
      line-height: 130%;
      margin: 0;
      strong {
        font-weight: 700;
      }
    }
    i {
      position: absolute;
      top: calc(100% + 0.625rem);
      left: -2rem;
      width: 7.5rem;
    }
    i img {
      width: 100%;
    }
    i img:nth-child(2) {
      display: none;
    }
  }
  &__img {
    position: absolute;
    top: 4rem;
    right: 0;
    width: 60.625rem;
    img {
      width: 100%;
    }
  }
}
.advantages {
  color: #ffffff;
  padding: 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 40%;
    margin-right: 2rem;
  }
  &__right {
    width: 60%;
    .advantages__text {
      display: none;
    }
    .advantages__btn {
      display: none !important;
    }
  }
  &__desc {
    max-width: 35rem;
  }
  h3 {
    font-size: 2.5rem;
    line-height: 120%;
    color: #ffffff;
    letter-spacing: -2px;
    span {
      color: #FDC32E;
      font-weight: 700;
    }
  }
  .subtitle {
    max-width: 22.5rem;
    padding-bottom: 0.625rem;
    margin-top: 1.875rem;
    &:after {
      background: #ffffff;
    }
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
  }
  &__text {
    max-width: 18rem;
    font-size: 1.125rem;
    line-height: 130%;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 17.25rem;
    margin-top: 3.125rem;
  }
  &__cards {
    max-width: 58.125rem;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    gap: 2.5rem 1.875rem;
    margin-left: auto;
  }
}
.advantage-card {
  min-height: 16.5625rem;
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;
  background-color: #ffffff;
  border-radius: 1.25rem;
  padding: 9.375rem 1.875rem 1.875rem;
  &__title {
    max-width: 23.125rem;
    font-size: 1.25rem;
    line-height: 130%;
    color: #000;
    strong {
      font-weight: 700;
      color: #FFB800;
      text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.10);
    }
  }
}
.products {
  padding: 6.25rem 0;
  h2 {
    max-width: 38.125rem;
    span {
      font-weight: 700;
      color: #DB5200;
    }
  }
  .subtitle {
    display: inline-block;
    margin-top: 3.125rem;
    &:after {
      background: #000000;
    }
    &:before {
      content: '';
      position: absolute;
      top: -1rem;
      right: 0;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    grid-auto-rows: 1fr;
    gap: 1.875rem;
    margin-top: 4.375rem;
  }
  &__img {
    position: absolute;
    top: -22rem;
    left: 0;
    width: 29.6875rem;
  }
}
.product-card {
  position: relative;
  background: radial-gradient(86.24% 72.09% at 87.55% 35.14%, #FFF 0%, rgba(255, 255, 255, 0.00) 70%), #F28D19;
  border: 0.0625rem solid #FFF;
  border-radius: 1.25rem;
  padding: 2.5rem 3.125rem 1.25rem;
  &__desc {
    position: relative;
    z-index: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__title {
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #FFF;
  }
  ul {
    max-width: 18.75rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 1.25rem;
    margin-bottom: auto;
    li {
      position: relative;
      font-size: 1.25rem;
      line-height: 130%;
      color: #FFF;
      border-bottom: 0.0625rem solid #ffffff;
      padding-left: 1.4375rem;
      padding-bottom: 0.3125rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0;
        width: 0.5rem;
        height: 0.5rem;
        background: #ffffff;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    width: 17.25rem;
    margin-top: 2rem;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 28.125rem;
    img:nth-child(2) {
      display: none;
    }
  }
}
.info {
  z-index: 4;
  background-image: url("../img/info-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  padding: 6.25rem 0 29.375rem;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    max-width: 49.6875rem;
    margin-right: 2rem;
  }
  &__right {
    position: relative;
    margin-left: auto;
    margin-top: 1.875rem;
  }
  h2 {
    span {
      color: #FDC32E;
    }
  }
  .subtitle {
    max-width: 28.75rem;
    margin-top: 5rem;
    &:after {
      background: #ffffff;
    }
    &:before {
      content: '';
      position: absolute;
      top: -1rem;
      right: 2.7rem;
      width: 100%;
      height: 0.0625rem;
      background: #ffffff;
    }
  }
  &__text {
    max-width: 27.8125rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 3.125rem;
    margin-top: 11.875rem;
    p {
      font-size: 1.125rem;
      line-height: 130%;
      margin: 0;
      span {
        color: #FDC32E;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 3.4375rem;
  }
  &__item {
    max-width: 21.875rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25rem;
    &:first-child {
      margin-left: -6rem;
    }
    &-title {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 600;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
    }
  }
  &__img {
    max-width: 25rem;
   display: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: -2.5rem;
    right: 100%;
    width: 56.25rem;
    img {
      width: 100%;
      max-height: unset;
      max-width: unset;
      pointer-events: none;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.advantages2 {
  padding: 0;
  margin-top: -1rem;
  h2 {
    position: relative;
    max-width: 39.6875rem;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: -3.125rem;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
    span {
      font-weight: 700;
      color: #DB5200;
    }
  }
  &__cards {
    max-width: 91.5625rem;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 3.75rem / 4));
    gap: 1.25rem;
    margin-top: 6.25rem;
  }
}
.advantage2-card {
  position: relative;
  display: flex;
  flex-direction: column;
  background: #FFF;
  border-radius: 1.25rem;
  padding: 1.875rem 1.875rem 2.75rem;
  &:nth-child(7),
  &:nth-child(8) {
    transform: translate(6.125rem, 0);
  }
  &:nth-child(11),
  &:nth-child(12) {
    transform: translate(-3.125rem, 0);
  }
  &__img {
    max-width: 9.25rem;
    height: 8.125rem;
    img {
      width: 100%;
    }
  }
  &__number {
    position: absolute;
    top: 1.875rem;
    right: 1.875rem;
    font-size: 3.125rem;
    line-height: 100%;
    font-weight: 900;
    color: transparent;
    -webkit-text-stroke: 0.0625rem #000;
    text-stroke: 0.0625rem #000;
    -webkit-background-clip: text;
    background-clip: text;
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 1.25rem;
  }
  &__title {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
  }
  &__text {
    font-size: 0.875rem;
    line-height: 130%;
    margin-top: 1.25rem;
    margin-bottom: auto;
  }
  &__hashtag {
    height: 2.75rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    line-height: 130%;
    background: #F0F0F0;
    border: 0.0625rem solid #FFF;
    border-radius: 0.625rem;
    padding: 0 0.625rem;
    margin-top: 1.25rem;
    margin-left: auto;
    span {
      font-size: 1rem;
      line-height: 130%;
    }
  }
}
.advantages3 {
  background-image: url("../img/advantages3-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 58rem 0 9.375rem;
  margin-top: -50rem;
  h2 {
    position: relative;
    max-width: 50rem;
    color: #ffffff;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: -3.125rem;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
    span {
      font-weight: 700;
    }
  }
  &__items {
    max-width: 90rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    margin: 8.75rem auto 0;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 9.375rem;
  }
  &__group:nth-child(2) {
    gap: 3.125rem;
  }
  &__item {
    max-width: 28.125rem;
    background: radial-gradient(83.62% 73.96% at 65.41% 42.41%, #FFF 0%, rgba(255, 255, 255, 0.00) 100%), linear-gradient(180deg, rgba(255, 255, 255, 0.50) 0%, rgba(255, 255, 255, 0.00) 100%);
    border-radius: 1.25rem;
    padding: 3.125rem;
    &-title {
      font-size: 1.875rem;
      line-height: 130%;
      font-weight: 600;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      margin-top: 1.25rem;
    }
  }
  &__bottom {
    max-width: 90rem;
    margin: 11.875rem auto 0;
  }
  .main__group {
    display: inline-flex;
    background: #FFF;
    border-radius: 1.25rem;
    padding: 2.5rem;
    margin-top: 0;
  }
  &__img {
    position: absolute;
    top: 50rem;
    right: 0;
    max-width: 120rem;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.exhibitions {
  padding: 9.375rem 0;
  &__inner {
    max-width: 36.5625rem;
  }
  h2 {
    position: relative;
    display: inline-flex;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: -3.125rem;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
    span {
      font-weight: 700;
      color: #DB5200;
    }
  }
  &__subtitle {
    font-size: 1rem;
    line-height: 130%;
    margin-top: 6.25rem;
  }
  &__btn {
    width: 17.25rem;
    margin-top: 3.125rem;
  }
  &__slider {
    margin-top: 6.25rem;
  }
  .slick-list {
    overflow: unset;
    margin: 0 -1.5625rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    width: auto !important;
    height: auto;
    margin: 0 1.5625rem;
    & > div {
      height: 100%;
    }
  }
  .slick-arrow {
    display: none;
  }
  &-img {
    img {
      width: 100%;
    }
  }
}
.about {
  background-image: url("../img/about-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 6.25rem 0 11.25rem;
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 37.5rem;
  }
  h2 {
    position: relative;
    color: #ffffff;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: -3.125rem;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
    span {
      font-weight: 700;
      color: #FDC32E;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875rem;
    margin-top: 6.25rem;
    p {
      font-size: 1rem;
      line-height: 130%;
      color: #ffffff;
      margin: 0;
    }
  }
  &__btn {
    width: 17.25rem;
    margin-top: 3.125rem;
  }
  &__img {
    position: absolute;
    right: 1.875rem;
    bottom: -7.8125rem;
    max-width: 73.125rem;
    img {
      width: 100%;
    }
  }
}
.connection {
  padding: 6.25rem 0 13.4375rem;
  .container-fluid {
    position: relative;
    z-index: 5;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    padding-left: 3.125rem;
    .connection__img {
      display: none;
    }
  }
  &__left {
    width: 50%;
    max-width: 46.25rem;
    margin-right: 2rem;
  }
  &__right {
    width: 50%;
  }
  h3 {
    font-size: 2.5rem;
    line-height: 120%;
    color: #000;
    letter-spacing: -2px;
    span {
      color: #DB5200;
      font-weight: 700;
    }
  }
  ul {
    max-width: 42.1875rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    background: #4C6575;
    border-radius: 1.25rem;
    padding: 1.875rem;
    margin-top: 5rem;
    li {
      position: relative;
      font-size: 1.125rem;
      line-height: 130%;
      color: #FFF;
      padding-left: 1.5rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.375rem;
        left: 0;
        width: 0.625rem;
        height: 0.625rem;
        background: #FFF;
      }
      span {
        font-weight: 700;
      }
    }
  }
  &__img {
    position: absolute;
    z-index: 4;
    left: 0;
    bottom: -3.75rem;
    max-width: 87.5rem;
    img {
      width: 100%;
    }
  }
}
.form {
  background: #FFF;
  border: 0.3125rem solid #FFF;
  box-shadow: 0 0.25rem 1.25rem 0 rgba(0, 0, 0, 0.10), 0px 4px 20px 0px rgba(0, 0, 0, 0.10) inset;
  border-radius: 0.625rem;
  padding: 3.125rem;
  &-head {
    max-width: 21.875rem;
    font-weight: 500 !important;
    color: #000;
    span {
      font-weight: 700;
      color: #DB5200;
    }
  }
  &_block {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25rem / 2));
    gap: 1.25rem;
    margin-top: 2.5rem;
  }
  &-group {
    background: transparent;
    border: none;
    margin: 0;
    label {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 500;
      color: rgba(0, 0, 0, 0.40);
      margin-bottom: 0.3125rem;
    }
    .opacity {
      opacity: 0;
      pointer-events: none;
    }
    input {
      width: 100%;
      height: 4.375rem;
      background: #F2F2F2;
      border-color: transparent;
      border-radius: 0.625rem;
      padding: 0 1.25rem;
      font-size: 0.875rem;
      line-height: 130%;
      color: #000000;
      &::placeholder {
        color: rgba(0, 0, 0, 0.70);
      }
    }
    select {
      width: 100%;
      height: 4.375rem;
      background: #F2F2F2;
      border-color: transparent;
      border-radius: 0.625rem;
      padding: 0 2rem 0 1.25rem;
      font-size: 0.875rem;
      line-height: 130%;
      color: #000000;
      appearance: none;
      background-image: url("../img/select-arrow.svg");
      background-repeat: no-repeat;
      background-position: top 50% right 1.25rem;
      background-size: 0.625rem 0.25rem;
    }
  }
  &-btn {
    width: 100%;
    height: 4.375rem !important;
  }
  .agreement-check {
    margin-top: 1.25rem;
    input {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    label {
      position: relative;
      font-size: 0.75rem;
      line-height: 130%;
      color: #000;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 0.875rem;
        height: 0.875rem;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 0.0625rem solid #C0C0C0;
        border-radius: 0.3125rem;
        padding: 0.125rem;
      }
    }
  }
}


@media screen and (max-width: 1800px) {
  .advantage2-card {
    &:nth-child(7),
    &:nth-child(8) {
      transform: translate(3.75rem, 0);
    }
  }
}

@media screen and (max-width: 1700px) {
  .advantages2 {
    &__cards {
      max-width: 100%;
    }
  }
  .advantage2-card {
    &:nth-child(7),
    &:nth-child(8),
    &:nth-child(11),
    &:nth-child(12) {
      transform: translate(0, 0);
    }
  }
  .about {
    &__img {
      bottom: -2rem;
      max-width: 56.25rem;
    }
  }
}

@media screen and (max-width: 1600px) {
  .main {
    padding-bottom: 16rem;
    &__img {
      width: 50rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    h1 {
      font-size: 5rem;
    }
    &__subtitle {
      font-size: 3.75rem;
    }
    &__text {
      font-size: 1.5rem;
    }
  }
  .connection {
    &__inner {
      padding-left: 0;
    }
    &__left {
      width: 45%;
    }
    &__right {
      width: 55%;
    }
    h3 {
      font-size: 2rem;
    }
    ul {
      padding: 1.875rem;
    }
    &__img {
      bottom: 0;
      max-width: 60rem;
    }
  }
  .form {
    &-group {
      input,
      select {
        height: 4rem;
      }
    }
    &-btn {
      height: 4rem !important;
    }
  }
}

@media screen and (max-width: 1399px) {
  .main {
    h1 {
      font-size: 3.75rem;
    }
    &__subtitle {
      font-size: 2.5rem;
      margin-top: 1rem;
    }
    &__text {
      font-size: 1.25rem;
      margin-top: 2rem;
      &:after {
        top: 0.375rem;
      }
    }
    &__group {
      margin-top: 2rem;
    }
    &__info {
      margin-left: 1.25rem;
    }
    &__img {
      top: 1.25rem;
      width: 40rem;
    }
  }
  .advantages {
    h3 {
      font-size: 2rem;
