h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Inter';
  margin: 0;
}
h2 {
  font-size: 3rem;
  line-height: 120%;
  letter-spacing: -2px;
}
.btn {
  display: flex !important;
  justify-content: center;
  align-items: center;
  span {
    min-width: unset;
    padding: 0;
  }
}
.btn-yellow {
  height: 4.75rem;
  background: linear-gradient(85deg, #FDC32E 22.29%, #FFE7A8 51.63%, #FFE6A6 73.01%, #FDC32E 91.85%);
  box-shadow: 0 -0.25rem 0 0 #D8B705 inset, 0px 3px 49px -3px #FF6074;
  border-radius: 1.25rem;
  overflow: hidden;
  &:hover {
    background: linear-gradient(85deg, #FDC32E 91.85%, #FFE6A6 73.01%, #FFE7A8 51.63%, #FDC32E 22.29%);
    img {
      transform: translate(0, -100%);
    }
  }
  span {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 500;
    color: #000;
  }
  img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 9.375rem;
    pointer-events: none;
    transition: all 0.3s;
  }
}
.subtitle {
  position: relative;
  font-size: 0.875rem;
  line-height: 130%;
  font-weight: 500;
  text-transform: uppercase;
  padding-left: 1.875rem;
  &:after {
    content: '';
    position: absolute;
    top: 0.1875rem;
    left: 0;
    width: 0.6875rem;
    height: 0.6875rem;
  }
}
.lines {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  max-width: 1815px;
  width: 100%;
  height: 100%;
  background: transparent;
  pointer-events: none;
  span {
    position: absolute;
    top: 0;
    width: 0.0625rem;
    height: 100%;
    display: block;
    background: #D6D6D6;
  }
  span:nth-child(1) {
    left: 3.125rem;
  }
  span:nth-child(2) {
    right: 3.125rem;
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
  .lines {
    span:nth-child(1) {
      left: 2.125rem;
    }
    span:nth-child(2) {
      right: 2.125rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .btn-yellow {
    height: 3.75rem;
    span {
      font-size: 1rem;
    }
  }
  .lines {
    span:nth-child(1) {
      left: 1.5rem;
    }
    span:nth-child(2) {
      right: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
  }
  .lines {
    span:nth-child(1) {
      left: 1rem;
    }
    span:nth-child(2) {
      right: 1rem;
    }
  }
}

@media screen and (max-width: 767px) {
  .lines {
    span:nth-child(1) {
      left: 0.5rem;
    }
    span:nth-child(2) {
      right: 0.5rem;
    }
  }
}
