html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: 'Inter', sans-serif;
  background: #FEFCF7;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1815px;
}
img {
  object-fit: cover;
}

@media screen and (max-width: 576px) {
  body {
    background: #FFF;
  }
}

@media screen and (max-width: 359px) {
  .container-fluid {
    padding: 0 1.5rem;
  }
}