.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  color: #ffffff;
  padding: 1.25rem 0;
}
.header-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-left: 1.25rem;
  }
  &__text {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 500;
    letter-spacing: -0.149px;
  }
  &__phone {
    font-size: 1.125rem;
    line-height: 130%;
    letter-spacing: -0.149px;
    text-transform: uppercase;
    border-right: 0.0625rem solid rgba(255, 255, 255, 0.20);
    padding-right: 1.25rem;
    &:hover {
      color: #FDC32E;
    }
  }
  &__btn {
    color: #ffffff;
    background: transparent;
    padding: 0 0 0 1.25rem;
    &:hover {
      color: #FDC32E;
    }
    span {
      font-size: 1rem;
      line-height: 130%;
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
    }
  }
}
.header-bottom {
  margin-top: 2.8125rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 13.4375rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .nav {
    position: relative;
    display: flex;
    align-items: stretch;
    margin-left: 1rem;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: calc(100% + 2.8125rem);
      height: 0.0625rem;
      background: #ffffff;
    }
  }
  .nav-link {
    position: relative;
    padding: 0 0.625rem 1.875rem 0.625rem;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: -0.09375rem;
      transform: translateX(100%);
      width: 100%;
      height: 0.25rem;
      background: #ffffff;
      opacity: 0;
      transition: all 0.3s;
    }
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }
  .nav-link:hover ~ .nav-item::after {
    transform: translateX(100%);
  }
  .nav-link.active::after,
  .nav-link:hover::after {
    transform: translateX(0%);
    opacity: 1;
  }
  .nav-item {
    position: relative;
    display: flex;
  }
  .nav-show {
    display: none;
  }
  .nav-hidden {
    .nav-link:after {
      bottom: -0.25rem;
    }
    .nav-link:first-child {
      padding-left: 0.625rem;
    }
  }
}
.header-mob {
  display: none;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    display: flex;
    align-items: center;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1rem;
  cursor: pointer;
  margin-left: 1.5rem;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.125rem;
    background: #ffffff;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.4375rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      bottom: 0.4375rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1450px) {
  .header-bottom {
    margin-top: 2rem;
    .nav {
      align-items: baseline;
    }
    .nav-item {
      z-index: 2;
      padding-left: 0.625rem;
      &:hover {
        .nav-hidden {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    .nav-show {
      display: block;
      font-size: 2rem;
      line-height: 100%;
      cursor: pointer;
    }
    .nav-hidden {
      position: absolute;
      top: 100%;
      right: 0;
      max-width: 8.75rem;
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75rem;
      background: rgba(32, 50, 62, 0.80);
      padding: 0.75rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      .nav-link {
        padding: 0;
      }
      .nav-link:first-child {
        padding-left: 0;
      }
      .nav-link:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .header-bottom {
    &__logo {
      width: 11.25rem;
    }
    .nav {
      &:after {
        width: calc(100% + 1.5rem);
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% - 2rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
  }
  .header__wrap {
    position: fixed;
    z-index: 8;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-image: url("../img/header-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 6.25rem 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 0;
    }
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  .header-top {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__right {
      order: -1;
      flex-direction: column;
      align-items: start;
      margin: 0 0 2rem 0;
    }
    &__phone {
      font-size: 1rem;
      padding-right: 0;
      border-right: none;
    }
    &__btn {
      padding: 0;
      margin: 0.625rem 0 0 0;
    }
  }
  .header-bottom {
    order: -1;
    margin: 0 0 3.125rem 0;
    &__inner {
      width: calc(100% + 1.5rem);
      padding-right: 1.5rem;
      overflow: hidden;
    }
    &__logo {
      display: none;
    }
    .nav {
      flex-direction: column;
      align-items: start;
      gap: 0.625rem;
      margin-left: 0;
      &:after {
        display: none;
      }
    }
    .nav-link {
      padding: 0 0 0.625rem !important;
      &:after {
        bottom: -0.03125rem !important;
      }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 64.6rem;
        height: 0.0625rem;
        background: #ffffff;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
    .active {
      font-weight: 500;
      &:before {
        opacity: 1;
      }
    }
    .nav-item {
      padding-left: 0;
    }
    .nav-show {
      display: none;
    }
    .nav-hidden {
      position: static;
      max-width: 100%;
      gap: 1.25rem;
      background: transparent;
      padding: 0;
      opacity: 1;
      pointer-events: all;
      .nav-link:after {
        display: block;
      }
    }
  }
  .header-mob {
    position: relative;
    z-index: 10;
    display: block;
    .header-bottom__logo {
      display: block;
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    &:after {
      width: calc(100% - 1rem);
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 1.125rem 0;
  }
  .header__wrap {
    padding-top: 5.5rem;
  }
  .header-top {
    &__phone {
      font-size: 0.875rem;
    }
  }
  .header-bottom {
    &__logo {
      width: 6.625rem;
    }
  }
  .burger {
    width: 1rem;
    height: 0.5rem;
    &--active {
      span:nth-child(1) {
        top: 0.15625rem;
      }
      span:nth-child(2) {
        bottom: 0.1875rem;
      }
    }
  }
}

@media screen and (max-width: 499px) {
  .header-bottom {
    &__inner {
      width: calc(100% + 1rem);
      padding-right: 1rem;
    }
  }
}